.App {
    text-align: center;
  }
  
  .App-logo {
    height: 40vmin;
    pointer-events: none;
  }
  
  @media (prefers-reduced-motion: no-preference) {
    .App-logo {
      animation: App-logo-spin infinite 20s linear;
    }
  }
  
  .App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
  }
  
  .App-link {
    color: #61dafb;
  }
  
  @keyframes App-logo-spin {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }
  
  .textarea {
    background-color: #eceff1;
    border: 1px solid #ccc;
    border-radius: 0;
    box-sizing: border-box;
    color: #2c3e50;
    font-size: 16px;
    letter-spacing: 1px;
    /* margin-bottom: 25px;
    margin-top: 2px; */
    padding: 8px 15px;
    width: 100%;
  }
  
  .action-button {
    background: #673ab7;
    border: 0;
    border-radius: 0;
    color: #fff;
    cursor: pointer;
    float: right;
    font-weight: 700;
    /* margin: 10px 0 10px 5px; */
    padding: 10px 5px;
    width: 100px;
  }
  
  #heading {
    text-transform: uppercase;
    color: #673AB7;
    font-weight: normal;
  }
  .verify-otp{
    display: flex;
    align-items: center;
    justify-content: center
  }
  .verify-otp input{
    max-width: 250px;
    margin-right: 20px;
  }